<template>
    <div id="app" class="page">
        <div class="content" :style="appSizeComputed">
            <page-loading @loadComplete="handleLoadComplete"></page-loading>
            <music-component></music-component>
        </div>
    </div>
</template>

<script>
import navi from './util/nav-controller.js';
import { getWindowSize } from './util/rem.js';
// pages 
import PageLoading from './components/PageLoading.vue';
import MusicComponent from './components/Music.vue';
export default {
  name: 'app',
  components: {
    PageLoading,
    MusicComponent,
  },
  data () {
    return {
      globalState: window.store,
      vh: 0,
    };
  },
  computed: {
    appSizeComputed () {
      return {
        '--px-content-height': `${this.vh}px`
      }
    },
  },
  created () {
    this.resize();
    window.onresize = this.resize;
  },
  methods: {
    //resize
    resize () {
      let { width, height } = getWindowSize(), vw = 0, vh = 0;
      if (navi.isPC) {
        vh = height * 0.8;
        vw = vh * 9 / 18;
      } else {
        vw = width;
        vh = height;
      }
      this.vh = vh;
      this.globalState.setContent({ vw, vh });

    },
    //loading结束
    handleLoadComplete () {

    },
  },
};
</script>

