<template>
    <BasePage>
        <div class="page abs bg" :style="{ backgroundImage: finalImageSrcBg }">
            <!-- <div>Loading {{ loadPercent }}%</div> -->
            <div class="test-box"></div>
            <div class="test-box2"></div>
        </div>
    </BasePage>
</template>

<script>
import BasePage from './BasePage.vue';
import imageLoad from '../util/image-load.js';
export default {
    name: 'pageLoading',
    components: {
        BasePage,
    },
    data() {
        return {
            sharedState: window.store.state,
            loadPercent: 0,
            publicPath: process.env.BASE_URL,
            imageBgSrc: 'imgs/bg-loading.jpg',
        };
    },
    computed: {
        finalImageSrcBg() {
            return `url(${this.publicPath}${this.imageBgSrc})`;
        },
    },
    created() {
        let _imageLoad = new imageLoad();
        _imageLoad
            .queueImage(this.sharedState.sourceArrayPreLoad)
            .imageLoadingProgressCallback(
                num => {
                    this.preloadPercent = Math.floor(num);
                },
                () => {
                    this.startLoading();
                }
            );
    },
    methods: {
        startLoading() {
            let _imageLoad = new imageLoad();
            _imageLoad
                .queueImage(this.sharedState.sourceArrayLoading)
                .imageLoadingProgressCallback(
                    num => {
                        this.loadPercent = Math.floor(num);
                    },
                    () => {
                        this.$emit('loadComplete');
                    }
                );
        },
    },
};
</script>

<style lang="scss" scoped>
.bg {
    background-size: 100% 100%;
    font-size: 0.2rem;
}
.test-box{
    width:4rem;
    height:4rem;
